import {ImageCropComponent} from './../../common/components/image-crop/image-crop.component';
import {ImageFileReader} from './../../common/utils/ImageFileReader';
import {of} from 'rxjs';
import {ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit, ViewChild} from '@angular/core';
import QrScanner from 'qr-scanner';
import {catchError, switchMap, tap} from 'rxjs/operators';
import {Title} from '@angular/platform-browser';

QrScanner.WORKER_PATH = '/assets/service-workers/qr-scanner-worker.min.js';

@Component({
  standalone: false,
  selector: 'app-qr',
  templateUrl: './qr.component.html',
  styleUrls: ['./qr.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class QrComponent implements OnInit {

  constructor(private readonly cdr: ChangeDetectorRef,
              private readonly titleService: Title) {
    titleService.setTitle('QR Code Reader');
  }
  qrResult: string;
  qrError: string;
  errImg: {imageData: string} ;

  isScanning = false;

  private cropComponent: ImageCropComponent;
  @ViewChild('cropComponent') set content(cropComponent: ImageCropComponent) {
    this.cropComponent = cropComponent;
  }
  ngOnInit(): void {
  }

  private onError(image: File, error: any) {
    return of(error)
    .pipe(
      tap((err) => {
        this.qrError = 'Unable to find qr code in the image';
        console.warn(err);
      }),
      switchMap(() => ImageFileReader.convertFileToImageBase64(image)),
      tap((img) => this.errImg = img),
    );
  }

  private onErrorB64(imageB64: string, error: any) {
    return of(error)
    .pipe(
      tap((err) => {
        this.qrError = 'Unable to find qr code in the image';
        console.warn(err);
      }),
      tap(() => this.errImg = {imageData: imageB64}),
    );
  }

  private scanFile(image: File) {
    of(1)
    .pipe(
      tap(() => {
        this.qrError = this.qrResult = '';
        this.isScanning = true;
        this.cdr.detectChanges();
      }),
      switchMap(() => QrScanner.scanImage(image)),
      tap(result => this.qrResult = result),
      catchError(error => this.onError(image, error)),
      tap(() => {
        this.isScanning = false;
        this.cdr.detectChanges();
      }),
    )
    .subscribe();
  }

  private scanFileB64(imageB64: string) {
    of(1)
    .pipe(
      tap(() => {
        this.qrError = this.qrResult = '';
        this.isScanning = true;
        this.cdr.detectChanges();
      }),
      switchMap(() => {
        const imgEle = document.createElement('img') as HTMLImageElement;
        imgEle.src = imageB64;
        return QrScanner.scanImage(imgEle)
      }),
      tap(result => this.qrResult = result),
      catchError(error => this.onErrorB64(imageB64, error)),
      tap(() => {
        this.isScanning = false;
        this.cdr.detectChanges();
      }),
    )
    .subscribe();
  }

  fileChanged(event: Event) {
    const loadedFileElement = event.target as HTMLInputElement;
    const file = loadedFileElement.files[0];
    this.scanFile(file);
  }

  onImageCrop(imgB64: string) {
    this.scanFileB64(imgB64);
  }

  onCropClicked() {
    if(!this.cropComponent) { return; }
    this.cropComponent.cropImage();
  }

}
